import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

import Auth from '../utils/auth';

function Header() {

  const [top, setTop] = useState(true);
  const logout = (event) => {
    event.preventDefault();
    Auth.logout();
  };

  // detect whether user has scrolled the page down by 10px 
  useEffect(() => {
    const scrollHandler = () => {
      window.pageYOffset > 10 ? setTop(false) : setTop(true)
    };
    window.addEventListener('scroll', scrollHandler);
    return () => window.removeEventListener('scroll', scrollHandler);
  }, [top]);  

  return (
    <header className={`fixed w-full z-30 md:bg-opacity-90 transition duration-300 ease-in-out ${!top && 'bg-white backdrop-blur-sm shadow-lg'}`} >
      <div className="max-w-6xl mx-auto px-5 sm:px-6">
        <div className="flex items-center justify-between h-16 md:h-20">

          {/* Site branding */}
          <div className="flex-shrink-0 mr-4">
            {/* Logo */}
            <Link to="/" className="block" aria-label="Cruip">
            <img
                className="h-8 w-8"
                src="./spaceship.png"
                alt="Space Frontiers"
                />
            </Link>
          </div> 

          {/* Site navigation */}
          <nav className="flex flex-grow">
            <ul className="flex flex-grow justify-end flex-wrap items-center">
              {Auth.loggedIn() ? (
                <>
                  <li>
                    <Link to="/account" className="btn-sm text-gray-200 bg-gray-900 hover:bg-gray-800 ml-3 capitalize">
                      <svg className="px-1 mx-1 motion-safe:animate-pulse" width="20" height="20" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path fill="#FFC95E" fill-rule="evenodd" d="M12 16.667 5 22l3-8-6-4.5h7.5L12 2l2.5 7.5H22L16 14l3 8z"/></svg>
                      {Auth.getProfile().data.first_name}
                      <svg className="px-1 mx-1 motion-safe:animate-pulse" width="20" height="20" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path fill="#FFC95E" fill-rule="evenodd" d="M12 16.667 5 22l3-8-6-4.5h7.5L12 2l2.5 7.5H22L16 14l3 8z"/></svg>

                      </Link>
                  </li>
                  {/* <li>
                    <Link className="btn-sm text-gray-200 bg-gray-900 hover:bg-gray-800 ml-3" onClick={Auth.logout}>Sign Out</Link>
                  </li> */}
                </>
              ) : (
                <>
                  <li>
                    <Link to="/signin" className="btn-sm text-gray-200 bg-gray-900 hover:bg-gray-800 ml-3">Sign in</Link>
                  </li>
                  <li>
                    <Link to="/signup" className="btn-sm text-gray-200 bg-gray-900 hover:bg-gray-800 ml-3">
                      <span>Sign up</span>
                      <svg className="w-3 h-3 fill-current text-gray-400 flex-shrink-0 ml-2 -mr-1" viewBox="0 0 12 12" xmlns="http://www.w3.org/2000/svg">
                        <path d="M11.707 5.293L7 .586 5.586 2l3 3H0v2h8.586l-3 3L7 11.414l4.707-4.707a1 1 0 000-1.414z" fillRule="nonzero" />
                      </svg>                  
                    </Link>
                  </li>
                </>
              )}
            </ul>
          </nav>
        </div>
      </div>
    </header>
  );
}

export default Header;
